.floating-button {
  position: fixed;
  right: 15px;
  bottom: 15px;
}
.floating-button__btn-default {
  position: relative;
  opacity: 0;
  z-index: 3;
}
.floating-button > div {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
}
.floating-button > div:first-child {
  position: relative;
  display: flex;
  align-items: flex-start;
  overflow: hidden;
}
.floating-button .btn {
  cursor: pointer;
}